#root {
  background-color: rgb(14, 41, 71);
  min-height: 350vh;
  display: flex;
  text-align: center;
  align-items:center;
  justify-content:center;
  color: white;
  
   }
  
  
 h1 {
  
  text-align:center;
  flex-direction: column;
  font-size: 45px;
  font-family: 'Tajawal', sans-serif;
  }
  
 h2{
  
  text-align: center;
  flex-direction: column;
  font-size: 17px;
  font-weight:lighter;
  font-family: 'Tajawal', sans-serif;
 margin-bottom: 350px;
  
  
 }
  
 h3 {
  
  
  text-align:right ;
  text-align-last: right;
  justify-content: right;
  font-family: 'Tajawal', sans-serif;
  font-size: 25px;
  min-width: 100px;
  
 }
  
 .wrapper {
  
  display: grid;
  grid-template-columns: auto auto auto;
 gap:40px;
 justify-content: center;
  
  
  
  
 }
  
 .card {
  
   overflow:hidden;
   min-width: 40rem;
   box-shadow: black;
  display:flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  
  
 }
 .card-image {
  
  height:12rem;
  width:200px;
  object-fit: cover;
  
  
  
  
  
  
 }
  
  
 h5{
  
  font-size: 15px;
  font-family: 'Tajawal', sans-serif;
  font-weight: normal;
  
 }
  
 h4 {
  
  text-align:right ;
  text-align-last: right;
  justify-content: right;
  font-family: 'Tajawal', sans-serif;
  font-size: 25px;
  min-width: 1000px;
  margin-top: 100px;
 }
  
  
 .opacity-pics{
  
  
  display: grid;
  grid-template-columns: auto auto auto ;
 gap:40px;
 justify-content: center;
 opacity: 0.5;
 position:relative;
 margin-bottom: 200px;
  
 }
  
  
 h7 {
  
  font-size: 10px;
 text-align: center;
 font-family: 'Tajawal', sans-serif;
 font-weight: lighter;

  
 }
 
 